import { Pipe, PipeTransform } from '@angular/core';

import { LanguageCode } from '../../../../graphql/graphql';
import language2Country from '../../../../assets/data/language-to-country.json';

@Pipe({
  name: 'emojiFlagFromLanguageCode',
})
export class EmojiFlagFromLanguageCodePipe implements PipeTransform {
  transform(value: LanguageCode): unknown {
    return language2Country[value]
      .toUpperCase()
      .replace(/./g, (char) =>
        String.fromCodePoint(char.charCodeAt(0) + 127397),
      );
  }
}
