import { Component, forwardRef, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { QueryRef } from 'apollo-angular';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  takeUntil,
  tap,
} from 'rxjs/operators';

import {
  Product,
  ProductTranslation,
  SearchProductsGQL,
  SearchProductsQuery,
  SearchProductsQueryVariables,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-product-select',
  templateUrl: './product-select.component.html',
  styleUrls: ['./product-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ProductSelectComponent),
      multi: true,
    },
  ],
})
export class ProductSelectComponent
  implements OnInit, OnDestroy, ControlValueAccessor {
  destroy$: Subject<boolean> = new Subject();
  translations:
    | Array<Pick<ProductTranslation, 'name' | 'product'>>
    | undefined = undefined;
  loading = false;
  text$: BehaviorSubject<string> = new BehaviorSubject('');
  buffer: Array<Pick<ProductTranslation, 'name' | 'product'>> = [];
  dataQueryRef:
    | QueryRef<SearchProductsQuery, SearchProductsQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  constructor(private searchProductsGQL: SearchProductsGQL) {
    this.text$
      .pipe(
        takeUntil(this.destroy$),
        debounceTime(250),
        distinctUntilChanged(),
        filter((term) => {
          if (term?.trim().length === 0) {
            this.buffer = [];
            return false;
          }
          return true;
        }),
        tap(() => {
          this.buffer = [];
          this.fetchMore();
        }),
      )
      .subscribe();
  }

  writeValue(
    obj: Array<Pick<ProductTranslation, 'name' | 'product'>> | undefined,
  ): void {
    if (this.translations !== obj) {
      this.translations = obj;
      this.handleModelChange();
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  handleModelChange(): void {
    this.onChange(this.translations);
  }

  onChange: (
    _: Array<Pick<ProductTranslation, 'name' | 'product'>> | undefined,
  ) => void = (
    _: Array<Pick<ProductTranslation, 'name' | 'product'>> | undefined,
  ) => {};

  onTouched: () => void = () => {};

  ngOnInit(): void {}

  handleSearch(value: string): void {
    this.text$.next(value);
  }

  fetchMore(): void {
    if (this.dataSubscription) {
      this.dataQueryRef?.refetch({
        text: this.text$.getValue(),
        first: 30,
        skip: this.buffer.length,
      });
    } else {
      this.dataQueryRef = this.searchProductsGQL.watch(
        {
          text: this.text$.getValue(),
          first: 30,
        },
        {
          notifyOnNetworkStatusChange: true,
        },
      );
      this.dataSubscription = this.dataQueryRef?.valueChanges
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ loading, data }) => {
          this.loading = loading;
          if (loading) {
            return;
          }
          this.buffer = this.buffer.concat(
            data.searchProducts.products.map((product) => ({
              name: product.translations?.[0]?.name,
              product: {
                // tslint:disable-next-line: deprecation
                id: product.id,
              } as Product,
            })),
          );
        });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
