import { Component, forwardRef, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { QueryRef } from 'apollo-angular';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  takeUntil,
  tap,
} from 'rxjs/operators';

import {
  Brand,
  BrandTranslation,
  OrderBy,
  SearchBrandsGQL,
  SearchBrandsQuery,
  SearchBrandsQueryVariables,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-brand-select',
  templateUrl: './brand-select.component.html',
  styleUrls: ['./brand-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BrandSelectComponent),
      multi: true,
    },
  ],
})
export class BrandSelectComponent
  implements OnInit, OnDestroy, ControlValueAccessor {
  destroy$: Subject<boolean> = new Subject();
  translation: Pick<BrandTranslation, 'name' | 'brand'> | undefined = undefined;
  loading = false;
  text$: BehaviorSubject<string> = new BehaviorSubject('');
  buffer: Array<Pick<BrandTranslation, 'name' | 'brand'>> = [];
  dataQueryRef:
    | QueryRef<SearchBrandsQuery, SearchBrandsQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  constructor(private searchBrandsGQL: SearchBrandsGQL) {
    this.text$
      .pipe(
        takeUntil(this.destroy$),
        debounceTime(250),
        distinctUntilChanged(),
        filter((term) => term?.trim().length > 0),
        tap(() => {
          this.buffer = [];
          this.fetchMore();
        }),
      )
      .subscribe();
  }

  ngOnInit(): void {}

  writeValue(obj: Pick<BrandTranslation, 'name' | 'brand'> | undefined): void {
    if (this.translation !== obj) {
      this.translation = obj;
      this.handleModelChange();
      if (!this.buffer.length && obj) {
        this.buffer.push(obj);
      }
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  handleModelChange(): void {
    this.onChange(this.translation);
  }

  onChange: (
    _: Pick<BrandTranslation, 'name' | 'brand'> | undefined,
  ) => void = (_: Pick<BrandTranslation, 'name' | 'brand'> | undefined) => {};

  onTouched: () => void = () => {};

  handleSearch(value: string): void {
    this.text$.next(value);
  }

  fetchMore(): void {
    if (this.dataSubscription) {
      this.dataQueryRef?.refetch(this.getVariables());
    } else {
      this.dataQueryRef = this.searchBrandsGQL.watch(this.getVariables(), {
        notifyOnNetworkStatusChange: true,
      });
      this.dataSubscription = this.dataQueryRef?.valueChanges
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ loading, data }) => {
          this.loading = loading;
          if (loading) {
            return;
          }
          this.buffer = this.buffer.concat(
            data.brands
              ?.filter((brand) => brand.translations?.length > 0)
              .map((brand) => ({
                name: brand.translations?.[0]?.name,
                brand: {
                  // tslint:disable-next-line: deprecation
                  id: brand.id,
                } as Brand,
              })),
          );
        });
    }
  }

  getVariables(): SearchBrandsQueryVariables {
    return {
      where: {
        translationsSome: {
          nameContains: this.text$.getValue(),
        },
      },
      first: 30,
      skip: this.buffer.length,
      orderBy: {
        id: OrderBy.Desc,
      },
    };
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
