import { Component, forwardRef, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { QueryRef } from 'apollo-angular';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import {
  takeUntil,
  debounceTime,
  distinctUntilChanged,
  filter,
  tap,
} from 'rxjs/operators';

import {
  IngredientTranslation,
  OrderBy,
  SearchIngredientTranslationsGQL,
  SearchIngredientTranslationsQuery,
  SearchIngredientTranslationsQueryVariables,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-ingredient-select',
  templateUrl: './ingredient-select.component.html',
  styleUrls: ['./ingredient-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => IngredientSelectComponent),
      multi: true,
    },
  ],
})
export class IngredientSelectComponent
  implements OnInit, OnDestroy, ControlValueAccessor
{
  destroy$: Subject<boolean> = new Subject();
  translations:
    | Array<Pick<IngredientTranslation, 'name' | 'ingredient'>>
    | undefined = undefined;
  loading = false;
  text$: BehaviorSubject<string> = new BehaviorSubject('');
  buffer: Array<Pick<IngredientTranslation, 'name' | 'ingredient'>> = [];
  dataQueryRef:
    | QueryRef<
        SearchIngredientTranslationsQuery,
        SearchIngredientTranslationsQueryVariables
      >
    | undefined;
  dataSubscription: Subscription | undefined;
  completed = false;

  constructor(
    private searchIngredientTranslationsGQL: SearchIngredientTranslationsGQL,
  ) {
    this.text$
      .pipe(
        takeUntil(this.destroy$),
        debounceTime(250),
        distinctUntilChanged(),
        filter((term) => term?.trim().length > 0),
        tap(() => {
          this.buffer = [];
          this.fetchMore();
        }),
      )
      .subscribe();
  }

  writeValue(
    obj: Array<Pick<IngredientTranslation, 'name' | 'ingredient'>> | undefined,
  ): void {
    if (this.translations !== obj) {
      this.translations = obj;
      this.handleModelChange();
      this.buffer.push(...(obj || []));
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  handleModelChange(): void {
    this.onChange(this.translations);
  }

  onChange: (
    _: Array<Pick<IngredientTranslation, 'name' | 'ingredient'>> | undefined,
  ) => void = (
    _: Array<Pick<IngredientTranslation, 'name' | 'ingredient'>> | undefined,
  ) => {};

  onTouched: () => void = () => {};

  ngOnInit(): void {}

  scrollToEnd(): void {
    if (!this.completed) {
    }
  }

  scroll({ end }: { end: number }): void {
    if (this.loading || this.completed || !this.text$.getValue()) {
      return;
    }

    if (end + 10 >= this.buffer.length) {
      this.fetchMore();
    }
  }

  handleSearch(value: string): void {
    if (value?.trim().length === 0) {
      this.buffer = [];
    }
    this.text$.next(value);
  }

  fetchMore(): void {
    if (this.dataSubscription) {
      this.dataQueryRef?.refetch({
        where: {
          nameContains: this.text$.getValue(),
        },
        first: 30,
        skip: this.buffer.length,
        orderBy: {
          id: OrderBy.Asc,
        },
      });
    } else {
      this.dataQueryRef = this.searchIngredientTranslationsGQL.watch(
        {
          where: {
            nameContains: this.text$.getValue(),
          },
          first: 30,
          orderBy: {
            id: OrderBy.Asc,
          },
        },
        {
          notifyOnNetworkStatusChange: true,
        },
      );
      this.dataSubscription = this.dataQueryRef?.valueChanges
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ loading, data }) => {
          this.loading = loading;
          if (loading) {
            return;
          }
          this.completed = data?.ingredientTranslations.length === 0;
          this.buffer = this.buffer.concat(
            ...(data.ingredientTranslations as any[]),
          );
        });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
