import { Injectable } from '@angular/core';

import { LanguageCode } from '../../../../graphql/graphql';

@Injectable()
export class LanguageService {
  private orders: Map<LanguageCode, number>;

  constructor() {
    this.orders = new Map([
      [LanguageCode.Vi, 1],
      [LanguageCode.En, 2],
      [LanguageCode.Ko, 3],
    ]);
  }

  pick<T extends { language: LanguageCode }>(
    languages: Map<LanguageCode, number>,
    array: Array<T>,
  ): T | undefined {
    if (!array?.length) {
      return undefined;
    }
    const priority = Number.MAX_SAFE_INTEGER;
    let value: T = array[0];
    array.forEach((entity) => {
      if (
        (languages.get(entity.language) || Number.MAX_SAFE_INTEGER) < priority
      ) {
        value = entity;
      }
    });
    return value;
  }

  sort<T extends { language: LanguageCode }>(array: Array<T>): Array<T> {
    return array.sort(
      (a, b) =>
        (this.orders.get(a.language) || Number.MAX_SAFE_INTEGER) -
        (this.orders.get(b.language) || Number.MAX_SAFE_INTEGER),
    );
  }
}
