import {
  Directive,
  ElementRef,
  HostListener,
  Input,
  OnChanges,
  Renderer2,
  SimpleChanges,
} from '@angular/core';

@Directive({
  selector: 'img[appImgFallback]',
})
export class ImgFallbackDirective implements OnChanges {
  @Input() appImgFallback: string[] | string = [];
  currentImgIndex = 0;

  constructor(private el: ElementRef, private renderer: Renderer2) {}

  @HostListener('error')
  error(): void {
    if (Array.isArray(this.appImgFallback)) {
      if (this.currentImgIndex >= this.appImgFallback.length) {
        return;
      }
      this.renderer.setAttribute(
        this.el.nativeElement,
        'src',
        this.appImgFallback[this.currentImgIndex],
      );
    } else if (this.currentImgIndex === 0) {
      this.renderer.setAttribute(
        this.el.nativeElement,
        'src',
        this.appImgFallback,
      );
    }
    this.currentImgIndex++;
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.currentImgIndex = 0;
  }
}
