import { Component, forwardRef, Input, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { QueryRef } from 'apollo-angular';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  takeUntil,
  tap,
} from 'rxjs/operators';

import {
  SearchTagTranslationsGQL,
  SearchTagTranslationsQuery,
  SearchTagTranslationsQueryVariables,
  TagTranslation,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-tag-select',
  templateUrl: './tag-select.component.html',
  styleUrls: ['./tag-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TagSelectComponent),
      multi: true,
    },
  ],
})
export class TagSelectComponent
  implements OnInit, OnDestroy, ControlValueAccessor {
  destroy$: Subject<boolean> = new Subject();
  translations:
    | Array<Pick<TagTranslation, 'content' | 'tag'>>
    | undefined = undefined;
  loading = false;
  text$: BehaviorSubject<string> = new BehaviorSubject('');
  buffer: Array<Pick<TagTranslation, 'content' | 'tag'>> = [];
  dataQueryRef:
    | QueryRef<SearchTagTranslationsQuery, SearchTagTranslationsQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  constructor(private searchTagTranslationsGQL: SearchTagTranslationsGQL) {
    this.text$
      .pipe(
        takeUntil(this.destroy$),
        debounceTime(250),
        distinctUntilChanged(),
        filter((term) => term?.trim().length > 0),
        tap(() => {
          this.buffer = [];
          this.fetchMore();
        }),
      )
      .subscribe();
  }

  writeValue(
    obj: Array<Pick<TagTranslation, 'content' | 'tag'>> | undefined,
  ): void {
    if (this.translations !== obj) {
      this.translations = obj;
      this.handleModelChange();
      if (!this.buffer.length && obj) {
        this.buffer.push(...obj);
      }
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  handleModelChange(): void {
    this.onChange(this.translations);
  }

  onChange: (
    _: Array<Pick<TagTranslation, 'content' | 'tag'>> | undefined,
  ) => void = (
    _: Array<Pick<TagTranslation, 'content' | 'tag'>> | undefined,
  ) => {};

  onTouched: () => void = () => {};

  ngOnInit(): void {}

  handleSearch(value: string): void {
    this.text$.next(value);
  }

  fetchMore(): void {
    if (this.dataSubscription) {
      this.dataQueryRef?.refetch({
        where: {
          contentContains: this.text$.getValue(),
        },
        first: 30,
        skip: this.buffer.length,
      });
    } else {
      this.dataQueryRef = this.searchTagTranslationsGQL.watch(
        {
          where: {
            contentContains: this.text$.getValue(),
          },
          first: 30,
        },
        {
          notifyOnNetworkStatusChange: true,
        },
      );
      this.dataSubscription = this.dataQueryRef?.valueChanges
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ loading, data }) => {
          this.loading = loading;
          if (loading) {
            return;
          }
          this.buffer = this.buffer.concat(data.tagTranslations as []);
        });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
