import { Component, forwardRef, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { QueryRef } from 'apollo-angular';
import { MentionOnSearchTypes } from 'ng-zorro-antd/mention';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  takeUntil,
  tap,
} from 'rxjs/operators';

import {
  MentionUserFieldsFragment,
  SearchUsersGQL,
  SearchUsersQuery,
  SearchUsersQueryVariables,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-user-select',
  templateUrl: './user-select.component.html',
  styleUrls: ['./user-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => UserSelectComponent),
      multi: true,
    },
  ],
})
export class UserSelectComponent
  implements OnInit, OnDestroy, ControlValueAccessor {
  destroy$: Subject<boolean> = new Subject();
  loading = false;
  mentionedUser?: MentionUserFieldsFragment;
  users: Array<MentionUserFieldsFragment> = [];
  text$: BehaviorSubject<string> = new BehaviorSubject('');
  buffer: Array<MentionUserFieldsFragment> = [];
  dataQueryRef:
    | QueryRef<SearchUsersQuery, SearchUsersQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  constructor(private searchUsersGQL: SearchUsersGQL) {
    this.text$
      .pipe(
        takeUntil(this.destroy$),
        debounceTime(250),
        distinctUntilChanged(),
        filter((term) => {
          if (term?.trim().length === 0) {
            return false;
          }
          return true;
        }),
        tap(() => {
          this.buffer = [];
          this.fetchMore();
        }),
      )
      .subscribe();
  }

  ngOnInit(): void {}

  writeValue(mentionedUser: MentionUserFieldsFragment): void {
    if (this.mentionedUser !== mentionedUser) {
      this.mentionedUser = mentionedUser;
      this.handleModelChange();
      if (mentionedUser && this.buffer?.length === 0) {
        this.buffer = [mentionedUser];
      }
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  handleModelChange(): void {
    this.onChange(this.mentionedUser);
  }

  onChange: (_: MentionUserFieldsFragment | undefined) => void = (
    _: MentionUserFieldsFragment | undefined,
  ) => {};

  onTouched: () => void = () => {};

  handleSearch(value: string): void {
    this.text$.next(value);
  }

  fetchMore(): void {
    if (this.dataSubscription) {
      this.dataQueryRef?.refetch({
        where: {
          accountStartsWith: this.text$.getValue(),
        },
        first: 30,
      });
    } else {
      this.dataQueryRef = this.searchUsersGQL.watch(
        {
          first: 30,
          where: {
            accountStartsWith: this.text$.getValue(),
          },
        },
        {
          notifyOnNetworkStatusChange: true,
        },
      );
      this.dataSubscription = this.dataQueryRef?.valueChanges
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ loading, data }) => {
          this.loading = loading;
          if (loading) {
            return;
          }
          if (data) this.buffer = this.buffer.concat(data.users);
        });
    }
  }

  valueWith(user: MentionUserFieldsFragment): string {
    return user.account;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
